'use client';

import { useDropzone } from 'react-dropzone';
import { useState, useEffect } from 'react';

interface FileConversionProps {
  onUpgradePrompt: () => void;
}

const FileConversion: React.FC<FileConversionProps> = ({ onUpgradePrompt }) => {
  const [conversionCount, setConversionCount] = useState<number>(0);
  const [isConverting, setIsConverting] = useState<boolean>(false);
  const [showUpgradePrompt, setShowUpgradePrompt] = useState<boolean>(false);
  
  // Retrieve conversion count from localStorage
  useEffect(() => {
    const storedCount = localStorage.getItem('conversionCount');
    if (storedCount) {
      setConversionCount(Number(storedCount));
    }

    const lastConversionDate = localStorage.getItem('lastConversionDate');
    if (lastConversionDate) {
      const today = new Date().toISOString().split('T')[0];
      const lastDate = new Date(lastConversionDate).toISOString().split('T')[0];
      
      if (today !== lastDate) {
        // Reset the conversion count every new day/week
        setConversionCount(0);
        localStorage.setItem('conversionCount', '0');
      }
    }
  }, []);

  const { getRootProps, getInputProps } = useDropzone({
    accept: {
      'video/mp4': ['.mp4'], // Accepts .mp4 files
    },
    onDrop: (acceptedFiles) => {
      if (conversionCount >= 3) {
        setShowUpgradePrompt(true); // Show upgrade prompt if limit reached
        onUpgradePrompt(); // Trigger the upgrade prompt logic
        return;
      }
      
      handleFileConversion(acceptedFiles[0]); // Process the file
    },
  });

  const handleFileConversion = (file: File) => {
    setIsConverting(true);

    // Simulate a 10-second conversion delay
    setTimeout(() => {
      console.log(`Converted file: ${file.name} to MP3`);
      
      // Increment conversion count
      const newCount = conversionCount + 1;
      setConversionCount(newCount);
      localStorage.setItem('conversionCount', newCount.toString());
      localStorage.setItem('lastConversionDate', new Date().toISOString());

      setIsConverting(false);
    }, 10000); // 10-second delay for conversion
  };

  return (
    <div>
      {/* DropZone */}
      <div
        {...getRootProps()}
        className="bg-gray-100 dark:bg-gray-700 bg-opacity-20 border-4 border-gray-300 dark:border-gray-600 border-dashed rounded-lg p-8 sm:p-12 md:p-16 text-gray-900 dark:text-white w-full sm:w-11/12 md:w-3/4 lg:w-2/3 mx-auto transition-all duration-300 hover:bg-gray-100/90 transform"
      >
        <input {...getInputProps()} />
        <p className="text-sm sm:text-base md:text-lg">Drag and drop your MP4 file here or click to select</p>
      </div>

      {/* Conversion Status */}
      {isConverting && <p className="text-gray-600 dark:text-gray-400 text-lg mt-4">Converting your file... This may take a few seconds.</p>}

      {/* Upgrade Prompt */}
      {showUpgradePrompt && (
        <div className="mt-6 p-4 bg-yellow-200 text-yellow-800 rounded-lg">
          <p className="text-lg">You have reached your 3 free conversions for the week. <strong>Upgrade to Pro</strong> for unlimited conversions!</p>
          <a href="#" className="text-blue-500 hover:underline">Upgrade Now</a>
        </div>
      )}
    </div>
  );
};

export default FileConversion;
